module.exports = "ul {\n  list-style-type: none; }\n\nbody {\n  font-family: Verdana, sans-serif; }\n\n/* Month header */\n\n.month {\n  padding: 70px 25px;\n  width: 100%;\n  text-align: center; }\n\n/* Month list */\n\n.month ul {\n  margin: 0;\n  padding: 0; }\n\n.month ul li {\n  color: white;\n  font-size: 20px;\n  text-transform: uppercase;\n  letter-spacing: 3px; }\n\n/* Previous button inside month header */\n\n.month .prev {\n  float: left;\n  padding-top: 10px; }\n\n/* Next button */\n\n.month .next {\n  float: right;\n  padding-top: 10px; }\n\n/* Weekdays (Mon-Sun) */\n\n.weekdays {\n  margin: 0;\n  padding: 0; }\n\n.weekdays li {\n  display: inline-block;\n  font-size: 13px;\n  width: 32px;\n  margin-right: 14px;\n  color: #c0c0c0;\n  text-align: center; }\n\n/* Days (1-31) */\n\n.days {\n  padding: 10px 0;\n  margin: 0; }\n\n.days li {\n  list-style-type: none;\n  display: inline-block;\n  margin-bottom: 14px;\n  margin-right: 14px;\n  color: #777;\n  width: 30px;\n  height: 30px;\n  border: rgba(155, 155, 155, 0.31) solid 1.2px;\n  border-radius: 5px;\n  text-align: center; }\n\n.days li span {\n    position: relative;\n    top: 7px;\n    color: #757575;\n    font-weight: bold; }\n\n.days li:hover {\n    cursor: pointer; }\n\n.selectedCalendarItem {\n  background-color: #0b7685; }\n\n.selectedCalendarItem span {\n    color: #fff !important; }\n\n/* Highlight the \"current\" day */\n\n.days li .active {\n  padding: 5px;\n  background: #1abc9c;\n  color: white !important; }\n"